import React from 'react';
import AppHeader from "../app-header/app-header";
import SearchPanel from "../search-panel/search-panel";
import TodoList from "../todo-list/todo-list";
import "./app.css"
import AddItem from "../add-item/add-item";


export default class App extends React.Component {

    maxId=1;
    state = {
        todoData: [
            this.createTodoItem('Drink Coffee'),
            this.createTodoItem('Make Awesome App'),
            this.createTodoItem('Have a lunch'),
        ],
        visiblfiltr:''
    };

    createTodoItem(label){
        return{
            label,
            important: false,
            done:false,
            id: this.maxId++
        }
    }

    addItem = (text) => {
       const newItem=this.createTodoItem(text);
       this.setState(({todoData})=>{

           const newArr=[
               ...todoData,
               newItem
           ];
           return{
               todoData:newArr
           }
       })
    };

    deleteItem=(id)=>{
        this.setState(({todoData})=>{
             const ids=todoData.findIndex((el)=>el.id===id);

             const arr=[
                 ...todoData.slice(0,ids),
                 ...todoData.slice(ids+1)
             ];
            return{
                todoData:arr
            }

        })
    };
    onToggleDone=(id)=>{
       this.setState(({todoData})=>{
           const idx=todoData.findIndex((el)=>el.id===id);
           const NewItem = {
               ...todoData[idx],
               done: !todoData[idx].done
           };

           const arr = [
               ...todoData.slice(0, idx),
               NewItem,
               ...todoData.slice(idx + 1)
           ];
           return{
               todoData:arr
           }
       })
    };
    onToggleImportant=(id)=>{
        this.setState(({todoData})=>{
            const idx=todoData.findIndex((el)=>el.id===id);
            const NewItem = {
                ...todoData[idx],
                important: !todoData[idx].important
            };
            const arr = [
                ...todoData.slice(0, idx),
                NewItem,
                ...todoData.slice(idx + 1)
            ];
            return{
                todoData:arr
            }
        })
    };

    search(item,term) {
        if (term.length===0){
          return item;
        }
      return   item.filter((item)=>{
            return item.label.indexOf(term)>-1;
        });
    }
    render() {
        const {todoData,visiblfiltr}=this.state;
        const visibilItems=this.search(todoData,visiblfiltr);

        const doneCount=todoData.filter((el)=>el.done).length;
        const done=todoData.length-doneCount;
        return (
            <div className="todo-app">
                <AppHeader toDo={doneCount} done={done}/>
                <SearchPanel/>
                <TodoList todos={visibilItems}
                          onDeleted={this.deleteItem}
                          onToggleDone={this.onToggleDone}
                          onToggleImportant={this.onToggleImportant}

                />

                <AddItem onItemAdded={this.addItem}/>
            </div>

        );

    };
};